                // if ($('div[inputname^="rxmvld"]').length > 0) {
                //     let rrr: any = window
                //     var rxmvRun = rrr.require('OrivRunMgr')
                //     rxmvRun.init($('html')[0].outerHTML)
                // }
                //初始化校验插件
                _fetchInitValue.call(this);
