define('rxmgsturd', [], function () {
    var g = {};
    //单击最大时长
    g.duration = 280;
    //长按的时长
    g.pressDuration = 320;
    //  判断swipe 在收到几个 move 时候开始
    g.swipeStartPos = 2;
    // 几个 touchmove 发出一次swipe
    g.swipeInterval = 1;
    //移动敏感度
    g.thresholdmove = 0.05;
    g.xyMulti = 2;
    return g;
});
